.App {
  text-align: center;
	min-height: 100vh;
	font-family: Arial, Helvetica, sans-serif;
}

.banner {
	width: 90%;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: white;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.dark_theme {
  background-color: #282c34;
  color: white;
}

.light_theme {
  background-color: #eee;
  color: #282c34;
}

.App-link {
  color: #61dafb;
}

.input_container {
  background-color: #eee;
  display: flex;
}

.input_dark_container {
  background-color: #282c34;
}

.myInput {
  display: block;
  padding: 10px 0;
  font-size: 20px;
  margin-bottom: 10px;
  border-radius: 10px;
  outline: none;
  padding-left: 10px;
  width: calc(100% - 10px - 4px);
}

.myInput:focus {
  background-color: rgb(234, 250, 255);
}

.myInput_dark_theme {
  background-color: #505050;
  color: #fff;
  border-color: #303030;
}

.myInput_dark_theme:focus {
  background-color: #606060;
}

.page-container {
  max-width: 1000px;
  min-width: 200px;
	padding-bottom: 10px;
	margin: 0 auto;
	margin-top: -5px;
}

.arrow-up {
  position: fixed;
  bottom: 20px; 
  right: 20px;
  font-size: 2em;
  width: 50px;
  height: 50px;
  line-height: 50px;
  border-radius: 50px;
  background-color: #ddd;
  color: #303030;
}

.arrow-up-dark-theme {
  background-color: #404040;
  color: #fff;
}

.arrow-up:hover {
  background-color: #ccc;
  cursor: pointer;
}

.arrow-up-dark-theme:hover {
  background-color: #505050;
}

.switchPageButtonsContainerDark {
  display: flex;
  justify-content: space-between;
  padding: 0;
}

.switchPageButtonsContainerLight {
  display: flex;
  justify-content: space-between;
  padding: 0;
}

.switchPageButtonsContainerDark > button {
  padding: 5px;
  flex: 1;
  box-sizing: border-box;
  margin: 0 10px;
  border: none;
  outline: none;
  background-color: #505050;
  color: #fff;
}

.switchPageButtonsContainerLight > button {
  padding: 5px;
  flex: 1;
  box-sizing: border-box;
  margin: 0 10px;
  border: none;
  outline: none;
  background-color: #ddd;
  color: #000;
}

.switchPageButtonsContainerLight > button:hover {
  background-color: #bbb;
  color: #000;
}

.switchPageButtonsContainerDark > button:hover {
  background-color: #404040;
  color: #fff;
}

.switchPageButtonsContainerDark > button.isActiveButtonDark {
  background-color: #21de7c;
  color: #000;
}

.switchPageButtonsContainerLight > button.isActiveButtonLight {
  background-color: #21de7c;
  color: #000;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@media only screen and (max-width: 768px) {
  .myInput {
    font-size: 16px;
	}
	
	.banner {
		width: 100%;
	}

  .arrow-up {
    bottom: 10px; 
    right: 10px;
    font-size: 1.5em;
    width: 40px;
    height: 40px;
    line-height: 40px;
    border-radius: 40px;
    background-color: rgba(221, 221, 221, 0.6);
  }

  .arrow-up:hover {
    background-color: rgba(204, 204, 204, 0.6);
  }

  .arrow-up-dark-theme {
    background-color: rgba(64, 64, 64, 0.6);
    color: #fff;
  }

  .arrow-up-dark-theme:hover {
    background-color: #30303099;
  }
}
