.message_container {
  background-color: #fff;
  padding: 10px;
  margin-bottom: 10px;
  text-align: left;
  border-radius: 20px;
  word-wrap: break-word;

  .message_flex {
    display: flex;
    justify-content: space-between;
  }

  .message_login {
    color: green;
    font-size: 18px;
    margin-bottom: 15px;
  }

  .message_currency {
    color: green;
    font-size: 20px;

    @media only screen and (max-width: 768px) {
      font-size: 16px;
    }
  }
  .message_date_prov {
    color: #a0a0a0;
    margin-bottom: 15px;

    @media only screen and (max-width: 768px) {
      font-size: 13px;
    }
  }

  .message_content {
    font-size: 20px;
		color: #202020;
		white-space: pre-wrap; //new line /n 

    a {
      color: blue;
    }

    @media only screen and (max-width: 768px) {
      font-size: 16px;
    }
  }
}

.message_container:nth-last-child(1) {
  margin-bottom: 0;
}

.message_container_dark_theme {
  background-color: #505050;

  .message_login, .message_currency {
    color: lightgreen;
  }

  .message_date_prov {
    color: #a0a0a0;
  }

  .message_content {
    color: #fff;

    a {
      color: lightblue;
    }
  }
}

.numberOfDonate {
  text-align: right;
  margin-top: 10px;

  @media only screen and (max-width: 768px) {
    font-size: 14px;
  }
}